<template>
  <div class="flex flex-col h-full justify-center mx-5 md:mx-40 lg:mx-72">
    <div class="text-3xl font-semibold self-center text-white">Rasteedi</div>
    <img class="object-contain w-32 rounded-3xl self-center my-20" src="./../assets/rt.png" alt="Logo" />
    <input
      class="rounded-xl border bg-gray-200 border-gray-500 focus:outline-none focus:ring focus:ring-blue-300 py-2 px-3"
      v-model="email"
      type="email"
      name="email"
      id="email"
      placeholder="Email"
    />
    <input
      class="
        rounded-xl
        border
        bg-gray-200
        border-gray-500
        focus:outline-none focus:ring focus:ring-blue-300
        py-2
        px-3
        mt-5
      "
      v-model="password"
      type="password"
      name="password"
      id="password"
      placeholder="Password"
      @keyup.enter="login()"
    />
    <button
      v-if="!loginFailed"
      class="
        transition
        duration-300
        bg-green-600
        text-white text-2xl text-center
        self-center
        mt-5
        py-1
        px-5
        rounded
        disabled:opacity-50
        hover:bg-green-800
      "
      :disabled="email == '' || password == ''"
      @click="login()"
    >
      Login
    </button>
    <div
      v-else
      class="relative py-3 pl-4 pr-10 leading-normal text-yellow-700 bg-yellow-100 rounded-lg mt-5"
      role="alert"
    >
      <p>Email oder Passwort nicht korrekt!</p>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useRouter } from "vue-router";
import { ApiRequest, API_URLS } from "@/api/request.api";

export default defineComponent({
  name: "Login",
  setup() {
    const email = ref("");
    const password = ref("");
    const loginFailed = ref(false);
    const router = useRouter();

    const login = async () => {
      const json = await new ApiRequest<
        { email: string; password: string },
        { data: { access_token: string; refresh_token: string } }
      >(API_URLS.LOGIN)
        .post()
        .body({ email: email.value, password: password.value })
        .execute();
      if (json) {
        localStorage.setItem("jwt", json.data["access_token"]);
        localStorage.setItem("refresh_token", json.data["refresh_token"]);
        router.push({ name: "Home" });
      } else {
        loginFailed.value = true;
        setTimeout(() => {
          loginFailed.value = false;
        }, 2000);
      }
    };

    return { email, password, login, loginFailed };
  },
});
</script>
